    meta: {
      name: pageName,
      navActiveLink: 'work-spaces',
      resource: 'MasterData',
      action: 'manage',
      breadcrumb: [
        {
          text: 'Master Data',
        },
        {
          text: pageName.plural,
          route: 'work-spaces',
        },
        {
          text: 'Edit',
          active: true,
        },
      ],
    },
